/** Hack to make Liqvid behave like {@link MediaElement}. */
export const PlaybackMEProxy = {
    get(target, p) {
        switch (p) {
            case "currentTime":
                return target.currentTime / 1000;
            case "duration":
                return target.duration / 1000;
            case "addEventListener":
                return (type, listener) => {
                    switch (type) {
                        case "seeking":
                            return target.on("seek", listener);
                        default:
                            return target.on(type, listener);
                    }
                };
            case "removeEventListener":
                return (type, listener) => {
                    switch (type) {
                        case "seeking":
                            return target.off("seek", listener);
                        default:
                            return target.off(type, listener);
                    }
                };
            default:
                return target[p];
        }
    },
    set(target, p, value) {
        switch (p) {
            case "currentTime":
                target.seek(value * 1000);
                break;
            case "duration":
                target.duration = value * 1000;
                break;
            default:
                target[p] = value;
        }
        return true;
    },
    // this is important so that we don't Proxy our Proxy
    has(target, p) {
        switch (p) {
            case "currentTime":
            case "duration":
            case "muted":
            case "pause":
            case "paused":
            case "play":
            case "playbackRate":
            case "seeking":
            case "volume":
            case "addEventListener":
            case "removeEventListener":
                return true;
            default:
                return false;
        }
    },
};
